@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 62.5%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.clock{width:100%;height:100vh;position:relative;overflow:hidden;transition:all 1s;-webkit-transition:all 1s}.clock__circle{position:absolute;transform:translate(-50%, -50%);-webkit-transform:translate(-50%, -50%);border-radius:100%;background-image:radial-gradient(white 0%, white 3%, #22345e 3%, #22345e 55%, #152552 55%);box-shadow:36px 14px 30px 9px #172f6a99,-44px -7px 40px 15px rgba(0,0,0,.25)}.clock__hand{position:absolute;transition:all .5s}.clock__hand--min{top:49.5%;width:25%;height:4px;background-color:#fff;z-index:3;left:50%;-webkit-transform-origin:left;transform-origin:left}.clock__hand--hour{top:49.5%;height:6px;width:20%;background-color:#fff;z-index:2;-webkit-transform:rotate(360deg);transform:rotate(360deg);-webkit-transform-origin:left;transform-origin:left;left:49.5%}.clock__hand--sec{top:50.1%;height:2px;width:30%;background-color:#da2e7c;left:49.4%;z-index:10;-webkit-transform-origin:0% 0%;transform-origin:0% 0%;z-index:1}.clock__digi{width:100%;position:absolute;bottom:0;left:0;flex-direction:column}.clock__digi--day{height:100%;font-size:1.4rem;text-transform:uppercase;font-weight:600;text-align:center}.clock__digi--time{font-size:4rem;text-align:center}.clock__digi--time span{min-width:2rem;display:inline-block;-webkit-animation:hidenohide 1s ease-in-out infinite backwards;animation:hidenohide 1s ease-in-out infinite backwards;margin-bottom:40px;text-align:center;font-size:4rem}.clock__theme-switch{position:absolute;right:2rem;bottom:2rem;width:10px}.clock #switch{display:none;transition:all 3s linear}.clock .switch-label{width:5rem;height:5rem;background-color:#639;position:relative}.clock .switch-label::after{transition:all 1s linear;position:absolute;content:"";display:block;border-radius:100%;background-color:#fff;background-image:url(/analog-clock/static/media/MoonStars.a821979f.svg);background-size:contain;background-repeat:no-repeat;background-position:center}.clock #switch:checked+.switch-label::after{background-color:#22345e;transform:rotate(360deg);-webkit-transform:rotate(360deg);background-image:url(/analog-clock/static/media/SunDim.e525186d.svg)}@-webkit-keyframes hidenohide{0%{opacity:0}100%{opacity:1}}@keyframes hidenohide{0%{opacity:0}100%{opacity:1}}@media(min-width: 320px){.clock__circle{width:70%;top:37%;left:50%;padding-top:70%}.clock .switch-label::after{top:-3rem;left:-1rem;width:3rem;height:3rem}}@media(min-width: 500px){.clock__circle{width:60%;top:37%;left:50%;padding-top:60%}}@media(min-width: 800px){.clock__circle{width:50%;top:37%;left:50%;padding-top:50%}.clock__digi--day{font-size:2rem}}@media(min-width: 900px){.clock__circle{width:40%;top:37%;left:50%;padding-top:40%}}@media(min-width: 1000px){.clock__circle{width:25%;top:37%;left:50%;padding-top:25%}}@media(min-width: 1204px){.clock__circle{width:25%;top:39%;left:50%;padding-top:24%}}@media(min-width: 1440px){.clock__circle{width:25%;top:37%;left:50%;padding-top:25%}.clock__digi--day{font-size:3rem}.clock__digi--time{font-size:5rem}.clock__digi--time span{font-size:4rem}}@media(min-width: 1648px){.clock__circle{width:33%;top:37%;left:50%;padding-top:33%}}
